/**
 *
 * Style for Document
 *
 * @author Takuto Yanagida
 * @version 2022-01-07
 *
 */


*,
::before,
::after {
	box-sizing: border-box;
}

html {
	scroll-behavior   : smooth;
	scroll-padding-top: 8rem;

	font-family: sans-serif;
	line-height: 1.8;
}

header,
main {
	margin-inline: auto;
	width : min(calc(100% - 2vw), 48rem);
}

header {
	position  : sticky;
	top       : 0;
	z-index   : 1;
	min-height: 6rem;

	border          : 1px solid #ddd;
	background-color: rgba(#fff, 0.5);
	backdrop-filter : blur(4px);
}

main {
	margin-block-start: 2rem;
}


// -----------------------------------------------------------------------------


div.blank {
	height: 10vh;
	background-image: repeating-linear-gradient(-45deg, transparent, transparent 6px, #defa 6px, #defa 12px);
}

ul, ol, dl {
	--nc-list-marker-color: #48f;
}

